<template>
  <div class="progress">
    <fx-card title="基础用法">
      <fx-progress :model-value="50"></fx-progress>
      <fx-progress :model-value="60" :format="format"></fx-progress>
      <fx-progress :model-value="70" type="success"></fx-progress>
      <fx-progress :model-value="80" type="warning"></fx-progress>
      <fx-progress :model-value="90" type="error"></fx-progress>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="内显文字">
      <fx-progress :model-value="50" inside></fx-progress>
      <fx-progress :model-value="40" type="success" inside></fx-progress>
      <fx-progress :model-value="30" type="warning" inside></fx-progress>
      <fx-progress :model-value="20" type="error" inside></fx-progress>
      <template #hide>
        <pre v-highlight="inside"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="自定义颜色">
      <fx-progress v-model="progress" :color="color1"></fx-progress>
      <fx-progress v-model="progress" :color="color2"></fx-progress>
      <fx-progress v-model="progress" :color="color3"></fx-progress>
      <fx-input-number v-model="progress" :step="5" :min="0" :max="100"></fx-input-number>
      <template #hide>
        <pre v-highlight="color"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="进度条宽度">
      <fx-progress :model-value="30" inside :stroke-width="30"></fx-progress>
      <fx-progress :model-value="25" type="success" inside :stroke-width="25"></fx-progress>
      <fx-progress :model-value="20" type="warning" inside :stroke-width="20"></fx-progress>
      <fx-progress :model-value="15" type="error" inside :stroke-width="15"></fx-progress>
      <template #hide>
        <pre v-highlight="width"><code></code></pre>
      </template>
    </fx-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from 'vue'

export default defineComponent({
  name: 'viewProgress',
  setup () {
    const progress = ref<number>(10)
    const color1 = reactive([
      {
        color: '#f56c6c',
        percentage: 20
      },
      {
        color: '#e6a23c',
        percentage: 40
      },
      {
        color: '#5cb87a',
        percentage: 60
      },
      {
        color: '#1989fa',
        percentage: 80
      },
      {
        color: '#6f7ad3',
        percentage: 100
      }
    ])
    const color3 = '#f60'
    const color2 = (percentage: number): string => {
      if (percentage < 30) {
        return '#909399'
      } else if (percentage < 70) {
        return '#e6a23c'
      } else {
        return '#67c23a'
      }
    }

    const format = (val: number) => {
      return val / 100
    }

    const base = `<fx-progress :model-value="50"></fx-progress>
<fx-progress :model-value="60" :format="format"></fx-progress>
<fx-progress :model-value="70" type="success"></fx-progress>
<fx-progress :model-value="80" type="warning"></fx-progress>
<fx-progress :model-value="90" type="error"></fx-progress>

export default defineComponent({
    setup() {
      const format = (val: number) => {
        return val / 100
      }
      return {
        format
      }
    }
})`
    const inside = `<fx-progress :model-value="50" inside></fx-progress>
<fx-progress :model-value="40" type="success" inside></fx-progress>
<fx-progress :model-value="30" type="warning" inside></fx-progress>
<fx-progress :model-value="20" type="error" inside></fx-progress>`
    const color = `<fx-progress v-model="progress" :color="color1"></fx-progress>
<fx-progress v-model="progress" :color="color2"></fx-progress>
<fx-progress v-model="progress" :color="color3"></fx-progress>
<fx-input-number v-model="progress" :step="5" :min="0" :max="100"></fx-input-number>

export default defineComponent({
    setup() {
       const progress = ref<number>(10)
       const color1 = reactive([
          {
            color: '#f56c6c',
            percentage: 20
          },
          {
            color: '#e6a23c',
            percentage: 40
          },
          {
            color: '#5cb87a',
            percentage: 60
          },
          {
            color: '#1989fa',
            percentage: 80
          },
          {
            color: '#6f7ad3',
            percentage: 100
          }
        ])
        const color3 = '#f60'
        const color2 = (percentage: number): string => {
          if (percentage < 30) {
            return '#909399'
          } else if (percentage < 70) {
            return '#e6a23c'
          } else {
            return '#67c23a'
          }
        }
      }
})`
    const width = `<fx-progress :model-value="30" inside :stroke-width="30"></fx-progress>
<fx-progress :model-value="25" type="success" inside :stroke-width="25"></fx-progress>
<fx-progress :model-value="20" type="warning" inside :stroke-width="20"></fx-progress>
<fx-progress :model-value="15" type="error" inside :stroke-width="15"></fx-progress>`

    return {
      color1,
      color2,
      color3,
      progress,
      base,
      inside,
      color,
      width,
      format
    }
  }
})
</script>

<style scoped lang="scss">
.progress {
}
</style>
